.infoTooltip {
  position: fixed;
  top: 0;
  bottom: 30%;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 0.3s, opacity 0.6s ease;
  z-index: 2;
  background: rgba(0, 0, 0, 0.9);

}
